import React from 'react';
import "./Card.css"

const Card = ({item,handleClick}) => {
  return (
    <div className="card" onClick={handleClick}>
      <div className="card__image-container">
        <img className="card__image" src={item.image} alt={item.title} />
      </div>
      <div className="card__info">
        <h2 className="card__title">{item.title}</h2>
        <div className='card-category'>{item.category}</div>
        <p className="card__description">{item.description}</p>
        <div className="card__rating">
          <p className="card__rating-count">{item.rating.count} reviews</p>
          <div className="card__rating-stars">
            {[...Array(Math.round(item.rating.rate))].map((_, i) => (
              <i key={i} className="fas fa-star"></i>
            ))}
          </div>
        </div>
        <h3 className="card__price">${item.price}</h3>
      </div>
    </div>
  );
};

export default Card;
