import React from 'react';
import { Route, Routes } from 'react-router-dom';
import Dashboard from './components/Dashboard';
import View from './components/View';

function App(props) {
  return (
    <Routes>
      <Route path='/' element={<Dashboard/>}/>
      <Route path='/:id' element={<View/>}/>
    </Routes>
  );
}

export default App;